import React, { Component } from 'react';
import { connect } from 'react-redux';

import xLabels from './ChartLineXLabels';
import Aux from '../../hoc/Aux/Aux';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import axios from '../../axios-orders';
import classes from './ChartLine.css'
import Chart from 'chart.js'
import dummyData from '../../store/dummy/dummyData';
import * as actions from '../../store/actions/index';



let myChart, myCircleChart, startDisplayText;
startDisplayText = 'Selecione um nó para visualizar';

export class chartLine extends Component {

    renderChart = () => {
        let ctx = document.getElementById('myChart').getContext('2d');
        myChart = new Chart(ctx, {
            type: 'line',
            data: {
                labels: xLabels,
                datasets: this.props.chartData,
            },
            options: {
                bezierCurve: true,
                responsive: true,
                hoverMode: 'index',
                stacked: false,
                hover: {
                    animationDuration: 0
                },
                legend: {
                    display: false,
                },
                scales: {
                    yAxes: [{
                        display: true,
                        id: 'y-axis-1',
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                        ticks: {
                            beginAtZero: true,
                            fontColor: "white"
                        },
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: true,
                            fontColor: "white",
                            padding: 16
                        },
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                    }],
                },
                tooltips: {
                    // yAlign: 'bottom',
                    bodySpacing: 6,
                    titleSpacing: 6,
                    footerSpacing: 6,
                    xPadding: 12,
                    yPadding: 12,
                    titleFontSize: 14,
                    bodyFontSize: 14,
                    backgroundColor: "black",
                    callbacks: {
                        label: function (tooltipItems, data) {
                            let text = `${data.datasets[tooltipItems.datasetIndex].label}  :  ${tooltipItems.value}`
                            return `  ${text.charAt(0).toUpperCase() + text.slice(1)}`
                        },
                    },
                    footerFontStyle: 'normal'
                },
            }
        });



    }


    renderCircleOptions() {
        const localProps = this.props;
        const dataToRender = dummyData;
        localProps.onTest(0, dataToRender[0]);

        let dataToCircle = [];
        let colorToCircle = [];
        let labelToCircle = [];
        //
        let indexLabelToCircle = [];
        //
        dataToRender.forEach((element) => {
            indexLabelToCircle.push(element.label);
        });

        let outros = dataToRender.filter((data) => {
            return (data.theme === 'outros' || data.theme === '');
        })

        let cartao = dataToRender.filter((data) => {
            return data.theme === 'cartão';
        })

        let conta = dataToRender.filter((data) => {
            return data.theme === 'conta';
        })

        let emprestimo = dataToRender.filter((data) => {
            return data.theme === 'empréstimo';
        })


        const mountThemesData = (element, color) => {
            dataToCircle.push(element.data.reduce((a, b) => a + b, 0));
            colorToCircle.push(color);
            labelToCircle.push(element.label);
        }

        outros.forEach((element) => {
            mountThemesData(element, 'rgba(99, 110, 250, 1)');
        });

        cartao.forEach((element) => {
            mountThemesData(element, 'rgba(239, 85, 59, 1)');
        });

        conta.forEach((element) => {
            mountThemesData(element, 'rgba(0, 204, 150, 1)');
        });

        emprestimo.forEach((element) => {
            mountThemesData(element, 'rgba(171, 99, 250, 1)');
        });


        let ctx2 = document.getElementById('myCircleChart').getContext('2d');
        myCircleChart = new Chart(ctx2, {
            type: 'pie',
            data: {
                datasets: [
                    {
                        data: dataToCircle,
                        backgroundColor: colorToCircle,
                        hoverBackgroundColor: colorToCircle,
                        borderWidth: 1,
                        borderColor: 'rgba(255, 255, 255, .2',
                        hoverBorderColor: 'rgba(255, 255, 255, 1',

                    },
                    // {
                    //     data: [510, 480],
                    //     backgroundColor: ['red', 'blue'],
                    //     hoverBackgroundColor: ['red', 'blue'],
                    //     borderWidth: 1,
                    //     borderColor: 'rgba(255, 255, 255, .2',
                    //     hoverBorderColor: 'rgba(255, 255, 255, 1',

                    // },
                ],
                labels: labelToCircle
            },
            options: {
                responsive: true,
                legend: {
                    display: false,
                },
                tooltips: {
                    // yAlign: 'bottom',
                    bodySpacing: 6,
                    titleSpacing: 6,
                    footerSpacing: 6,
                    xPadding: 12,
                    yPadding: 12,
                    titleFontSize: 14,
                    bodyFontSize: 14,
                    backgroundColor: "black",
                    footerFontStyle: 'normal'
                },
            },
        });

        

        document.getElementById('myCircleChart').onclick = function (evt) {

            var activePoints = myCircleChart.getElementsAtEvent(evt);
            let selectedItem = null;
            let selectedLabel = '';
            //

            if (activePoints[0] !== undefined) {

                if (activePoints[0]._view.label) {
                    selectedLabel = activePoints[0]._view.label;
                }
                selectedItem = dataToRender[indexLabelToCircle.indexOf(selectedLabel)];
                selectedItem.active = !selectedItem.active;
                //
                startDisplayText = selectedLabel;
                document.getElementById('chartLineCircleTip').style.display = 'none';
                localProps.onTest(indexLabelToCircle.indexOf(selectedLabel), selectedItem);
            } else {
                //(activePoints[0]);
            }


        }
    }

    componentDidMount() {
        this.renderChart();
        this.renderCircleOptions();
    }

    componentWillReceiveProps() {
        setTimeout(() => {
            myChart.data.datasets = this.props.chartData;
            myChart.update();
        }, 100);
    }


    render() {

        return (
            <Aux>
                <div className={classes.ChartLine}>
                    <div className={classes.ChartLineTop}>
                        <h1>{startDisplayText}</h1>
                        <section>

                            <span>
                                <div style={{ backgroundColor: '#636efa' }}></div>
                                <p>Outros</p>
                            </span>

                            <span>
                                <div style={{ backgroundColor: '#ef553c' }}></div>
                                <p>Cartão</p>
                            </span>

                            <span>
                                <div style={{ backgroundColor: '#3ecc96' }}></div>
                                <p>Conta</p>
                            </span>

                            <span>
                                <div style={{ backgroundColor: '#ab63f9' }}></div>
                                <p>Empréstimo</p>
                            </span>
                        </section>



                    </div>

                    <div style={{ width: '100%', height: '100%', position: 'relative', float: 'left' }}>
                        <div style={{ width: '50%', height: '100%', left: '0px', top: '0px', position: 'absolute', visibility: 'visible' }}>
                            <canvas id="myChart"></canvas>
                        </div>
                        <div style={{ width: '50%', height: '75%', right: '0px', top: '0px', position: 'absolute' }}>
                            <p id="chartLineCircleTip" className={classes.ChartLineCircleTip}>Selecione um nó para visualizar</p>
                            <canvas style={{ width: '100%', height: '100%', top: '0px' }} id="myCircleChart"></canvas>
                        </div>
                    </div>

                </div>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        chartData: state.chartLine.chartData,
        up: state.chartLine.up,
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onTest: (id, active) => dispatch(actions.fetchIngredientsFailed(id, active)),
        onSelectNoneItems: (items) => dispatch(actions.selectNone(items)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(chartLine, axios));


