import nodesNames from './nodesNames'
import nodesValues from './nodesValues'
import nodesThemes from './nodesThemes'

//
const genRandomRgbaColor = (opacity, theme) => {
    let colorToItem = `rgba(${Math.round(Math.random() * 122) + 123}, ${Math.round(Math.random() * 122) + 123}, ${Math.round(Math.random() * 122) + 123} , ${opacity})`
    //
    switch (theme) {
        case 'outros':
            colorToItem = `rgba(99, 110, 250, ${opacity}`
            break;
        case 'cartão':
            colorToItem = `rgba(239, 85, 59, ${opacity}`
            break;
        case 'conta':
            colorToItem = `rgba(0, 204, 150, ${opacity}`
            break;
        case 'empréstimo':
            colorToItem = `rgba(171, 99, 249, ${opacity}`
            break;
        default:
            colorToItem = `rgba(171, 99, 250, ${opacity}`
    }
    return colorToItem;

}
const dummyData = [];
//
const render_nodesNames = nodesNames;
const render_nodesValues = nodesValues;
const render_nodesThemes = nodesThemes;

render_nodesNames.forEach((nodeName, index) => {
    let countIndex = index;
    dummyData.push(
        {


            label: `${nodeName.split("_").join(" ").charAt(0).toUpperCase() + nodeName.slice(1).split("_").join(" ")}`,
            id: +countIndex++,
            active: (index >= 6
                ? false
                : false),
            lineTension: 0,
            theme: render_nodesThemes[index],
            data: render_nodesValues[index],
            fill: true,
            hidden: false,
            backgroundColor: genRandomRgbaColor('.5', render_nodesThemes[index]),
            borderColor: genRandomRgbaColor('1', render_nodesThemes[index]),
            borderWidth: 3,
            yAxisID: 'y-axis-1',
        })
});




//




export default dummyData;


